@font-face {
  font-family: "asteain";
  src: url("../fonts/asteain.woff");
}

* {
  margin: 0;
  padding: 0;
  line-height: 2rem;
  font-family:  "Noto Sans JP", "asteain", "Hiragino Kaku Gothic ProN", Meiryo, sans-serif;
  }

hr {
    display: block; /* ブロックレベル要素として表示 */
    margin-top: 0.5em; /* 上のマージン */
    margin-bottom: 0.5em; /* 下のマージン */
    margin-left: auto; /* 左のマージンを自動調整 */
    margin-right: auto; /* 右のマージンを自動調整 */
    border-style: inset; /* 線のスタイル */
    border-width: 1px; /* 線の太さ */
  }

h1 {
  text-align: center;
  color: #fef4f4;
  background-color: #1e2a33;
}

.current {
    color: green;
  }

.pcolor {
    color: #fef4f4;
  }

.tcolor {
   font-weight: 100;
    color: #000000;
}

.de_h2 {
  font-weight: 100;
  color: #fef4f4;
}

.de_h3 {
  font-weight: 400;
  color: #fef4f4;
}

.center {
  text-align: center;
}

.c2 {
  background-color: #3b5566;
}

.c3 {
  background-color: #597f99;
}

.c4 {
  background-color: #76aacc;
}

.c5 {
  background-color: #89c3eb;
}

.c6 {
  background-color: #94d4ff;
}

.c7 {
  background-color: #eaf4fc;
}

.clock {
  text-align: right;
  font-weight: 100;
  color: #fef4f4;
  background-color: #ba2636;
  display: flex;
  justify-content: space-between;
}

.blog{
  border: 3px ridge #ffffff; /* 黒色の1ピクセルの固定枠 */
  padding: 0.5em;
  margin : 2em;
  background-color: #1e2a33;
  color: #fef4f4;
}

.tanka{
  border: 3px ridge #ffffff; /* 黒色の1ピクセルの固定枠 */
  padding: 0.5em;
  margin : 2em;
  background-color: #1e2a33;
  color: #fef4f4;
  font-weight: 100;
}

.staff_h2{
  color: black;
}

.staff_h3{
  color: black;
}

table , td, th {
	border: 1px solid #595959;
	border-collapse: collapse;
}
td, th {
	padding: 3px;
	width: 30px;
	height: 25px;
  white-space: nowrap;
}
th {
	background: #f0e6cc;
}
.even {
	background: #fbf8f0;
}
.odd {
	background: #fefcf9;
}

ol{
  padding-left: 50px;
}